.signup-wapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.signup-wapper form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.signup-wapper form button {
  font-size: 18px;
}

.signup-wapper input {
  font-size: 18px;
  margin: 10px;
}

.role-wrapper {
  display: flex;
  align-items: center;
  margin: 10px;
}
.role-label {
  font-size: 18px;
  margin: 10px;
}
.container {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 35px;
  margin-right: 10px;
  margin-bottom: 18px;
  cursor: pointer;
  font-size: 18px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: #2196f3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

.account-created-success {
  font-size: 18px;
  color: green;
}
